@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

* {
  padding: 0;
  margin: 0;

  font-family: "Quicksand";
  color:white;
}

.App {
  width: 100%;
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  background: linear-gradient(130deg, #836273, #343050);
}

.buttons {
  width: 280px;
  height: 18%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  button {
    width: 100%;
    height: 28%;

    font-size: 20px;
    font-weight: bolder;
    color: black;
    background-color: white;
    border: 1px solid black;
    cursor: pointer;
    border-radius: 5px;

    &:hover {
      transform: scale(1.05);
    }
  }
}

.card {
  position: relative;
  width: 280px;
  height: 450px;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;

  border-radius: .7rem;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

  .title {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;

    text-align: center;

    h2 {
      font-size: 60px;
      font-weight: lighter;
      line-height: 100%;
    }

    p {
      width: 100%;
      font-weight: 500;
    }
  }

  .data {
    width: 90%;
    height: 25%;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    h2 {
      width: 100%;
      height: 20%;

      font-size: 14px;
      font-weight: 400;
      border-bottom: 2px solid rgba(255, 255, 255, 0.214);
    }

    .every-hours {
      width: 100%;
      height: 80%;

      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}
